body {
  font-family: Arial, sans-serif;
}

.App {
  text-align: center;
  padding: 20px;
}

form {
  margin-top: 20px;
}

input, select, button {
  margin: 5px;
  padding: 8px;
}

/* Table styles */
table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px auto;
}

th, td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
  white-space: nowrap;
}

th {
  background-color: #f2f2f2;
  font-weight: bold;
}

td input[type="checkbox"] {
  margin-left: 8px;
}

td span {
  margin-left: 8px;
  font-style: italic;
  font-weight: bold;
}

.big-button {
  padding: 20px 40px;
  font-size: 24px;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 15px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.big-button:hover {
  background-color: #45a049;
}

h2 {
  text-align: center;
  color: #444;
}

.import-button {
  padding: 10px 15px;
  font-size: 16px;
  color: #ffffff;
  background-color: #4CAF50;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  margin: 10px;
}

.back-button {
  padding: 10px 15px;
  font-size: 16px;
  color: #ffffff;
  background-color: #f0ad4e;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  margin: 10px;
}

.delete-button {
  padding: 10px 15px;
  font-size: 16px;
  color: #ffffff;
  background-color: #636ca5;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  margin: 10px;
}

.export-button {
  padding: 10px 15px;
  font-size: 16px;
  color: #ffffff;
  background-color: #bd694b;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  margin: 10px;
}

.import-button:hover,
.back-button:hover,
.delete-button:hover,
.export-button:hover {
  opacity: 0.8;
}

.import-button:focus,
.back-button:focus {
  outline: none;
  box-shadow: 0 0 0 3px rgba(76, 175, 80, 0.5);
}

.return-button {
  padding: 10px 15px;
  font-size: 16px;
  color: #ffffff;
  background-color: #d4a5bd;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  margin: 15px;
}

.return-button:hover {
  opacity: 0.8;
}

.daily-cleaning-controls {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
}

/* Style for file input */
.file-input-wrapper {
  position: relative;
  overflow: hidden;
  display: inline-block;
  margin-right: 10px;
}

.file-input-wrapper input[type=file] {
  position: absolute;
  left: 0;
  top: 0;
  opacity: 0;
  cursor: pointer;
  width: 100%;
  height: 100%;
}

.details-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  position: relative;
}

.title-container {
  flex-grow: 1;
  text-align: center;
}

.button-container {
  position: absolute;
  right: 0;
  justify-content: center;
  align-items: center;
}

.details-header h3 {
  margin: 0;
  display: inline-block;
}

.date-selection-row {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  margin-bottom: 10px;
  font-weight: bold;
}

.date-selection-row span {
  margin-right: 10px;
}

.date-selection-row input[type="date"] {
  margin-right: auto;
}
